/* eslint-disable no-console */

import { register } from 'register-service-worker'

if (process.env.NODE_ENV === 'production') {
  register(`${process.env.BASE_URL}service-worker.js`, {
    ready() {
      console.log(
        'App is being served from cache by a service worker.\n' +
          'For more details, visit https://goo.gl/AFskqB'
      )
    },
    registered() {
      console.log('Service worker has been registered.')
    },
    cached() {
      console.log('Content has been cached for offline use.')
    },
    updatefound() {
      // new content clear cache so user gets the new version
      // caches.keys().then((cacheNames) => {
      //   cacheNames.forEach((cacheName) => {
      //     caches.delete(cacheName)
      //   })
      // })
      console.log('New content is downloading.')
    },
    updated(registration) {
      console.log('New content is available; please refresh.')
      // let cacheV = localStorage.getItem('cache')
      // console.log(cacheV, parseInt(cacheV))
      // if (!cacheV || parseInt(cacheV) < 1) {
      // caches.keys().then(function (cacheNames) {
      //   cacheNames.forEach(function (cacheName) {
      //     caches.delete(cacheName)
      //   })
      // })
      //   console.log('update insu')
      //   localStorage.setItem('cache', '1')
      // }
      document.dispatchEvent(
        new CustomEvent('swUpdated', { detail: registration })
      )
    },
    offline() {
      console.log(
        'No internet connection found. App is running in offline mode.'
      )
    },
    error(error) {
      console.error('Error during service worker registration:', error)
    }
  })
}
