.sidenav {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100vh;
  z-index: 100;
  visibility: hidden;
  opacity: 0;
  transition: all 0.6s;
  &.active {
    left: 0;
    visibility: visible;
    opacity: 1;
  }
  &__block {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    width: 10rem;
    height: 100%;
    overflow-y: auto;
    padding-top: 4.5rem;
    // padding-left: 2.5rem;
    transition: all 0.6s;
    z-index: 10;
    &-close {
      position: absolute;
      top: 1.6rem;
      right: 1.6rem;
      cursor: pointer;
    }

    .mobinfo,
    .mobgraph {
      margin: 2rem 0;
      img {
        display: block;
        margin: 0 auto;
        width: 3rem;
        cursor: pointer;
      }
    }
  }
  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.5;
  }
}

