<template>
  <section class="shedule">
    <div class="container container_sec" :class="dayNight">
      <div class="shedule__litera" :class="bookDN">
        {{ msg.isLitera }}
      </div>
      <div style="flex: 1">
        <div v-for="chService in msg.parse" :key="chService">
          <div
            v-for="(arrayBooks, nameService) in chService"
            :key="nameService"
          >
            <div
              :class="{
                servKind: nameService.length < 15 ? true : '',
                velPyatok: nameService.length > 15 ? true : ''
              }"
            >
              {{ nameService }}
            </div>
            <div
              class="bookname"
              :class="this.msg.isDay ? 'txtDay' : 'txtNight'"
              v-for="book in arrayBooks"
              :key="book"
            >
              <span>{{ book.boo }}&nbsp;</span>
              <span>{{ book.z[0] }}&nbsp;</span>
              <span @click="toText(book)">{{ book.z[1] }}</span>
            </div>
          </div>
        </div>
      </div>
      <div class="shedule__soc">
        <div class="soc-wrap">
          <div id="share-network-list" v-show="isOpenA">
            <ShareNetwork
              v-for="network in networks"
              :network="network.network"
              :key="network.network"
              :style="{ backgroundColor: network.color }"
              :url="sharing.url"
              :title="sharing.title"
              :description="sharing.description"
