              </template>
            </ShareNetwork>
          </div>
          <div class="share">
            <button
              class="share__btn"
              type="button"
              aria-label="Поделиться"
              @click="social"
            ></button>
          </div>
        </div>
      </div>
    </div>
    <transition name="modal">
      <div @click="social" class="overlay" v-if="isOpenA"></div>
    </transition>
  </section>
</template>

<script>
export default {
  props: {
    msg: Object
  },
  emits: ['toReading'],
  data() {
    return {
      isOpenA: false,
      sharing: {
        url: 'https://zachalo.web.app',
        title: 'Зачало | Богослужебные чтения на каждый день',
        description: 'Для просвещения в разуме.'
      },
      networks: [
        {
          network: 'email',
          name: 'Email',
          color: '#333333'
        },
        {
          network: 'odnoklassniki',
          name: 'Odnoklassniki',
          color: '#ed812b'
        },
        {
          network: 'telegram',
          name: 'Telegram',
          color: '#0088cc'
        },
        {
          network: 'viber',
          name: 'Viber',
          color: '#59267c'
        },
        {
          network: 'vk',
          name: 'Vk',
          color: '#4a76a8'
        },
        {
          network: 'whatsapp',
          name: 'Whatsapp',
          color: '#25d366'
        }
      ]
    }
  },
  methods: {
    // книга, что пробрасывается в App после клика на зачало, потом в TheReading
    toText(book) {
      this.$emit('toReading', book)
    },
    social() {
      this.isOpenA = !this.isOpenA
    }
  },
  computed: {
    dayNight() {
      return this.msg.isDay ? 'fonday' : 'fonnight'
    },
    bookDN() {
      return {
        txtDay: this.msg.isDay,
        txtNight: !this.msg.isDay,
        rus: this.msg.isLitera && !this.msg.isLitera.includes('j')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.shedule {
  display: flex; // внутри содержится section с flex=1

  &__litera {
    text-align: center;
    margin-bottom: 0.5rem;
    font-family: 'Hirmos Ucs', sans-serif;
    font-size: 1.75rem;
  }

  .rus {
    font-family: sans-serif;
    font-size: 1.5rem;
  }

  .servKind {
    text-align: left;
    margin: 0;
    font-size: 1.5rem;
    font-weight: bold;
    color: #b22222;
  }

  .velPyatok {
    text-align: center;
    margin: 0;
    font-size: 1.5rem;
    font-weight: bold;
    color: green;
  }

  .bookname {
    line-height: 2.75rem;

    :nth-child(1) {
      font-size: 1.4rem;
    }
    :nth-child(2) {
      font-size: 1.4rem;
    }
    :nth-child(3) {
      font-size: 1.4rem;
      text-decoration: underline;
      cursor: pointer;
    }
  }

  &__soc {
    align-self: flex-end;
    .soc-wrap {
      display: flex;
      justify-content: center;
      position: relative;

      #share-network-list {
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        align-self: flex-end;
        position: absolute;
        right: 0;
        bottom: 0;
        margin-bottom: 4.75rem;
        a[class^='share-network-'] {
          flex: none;
          border-radius: 3px;
          overflow: hidden;
          display: flex;
          flex-direction: row;
          align-content: center;
          align-items: center;
          cursor: pointer;
          margin: 5px;
        }
        a[class^='share-network-'] svg {
          margin: 0.5rem;
          flex: 0 1 auto;
          width: 1.75rem;
          height: 1.75rem;
        }
      }
    }
    .share {
      width: 3.5rem;
      height: 3.5rem;
      border-radius: 50%;
      // background-color: violet;
      background-color: #f9a825;
      background-size: 100%;
      cursor: pointer;
      transition: all 0.2s ease;
      box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px,
        rgba(0, 0, 0, 0.22) 0px 15px 12px;
      z-index: 2;
      margin-bottom: 1rem;
      &__btn {
        background-image: url('@/assets/images/share.svg');
        background-size: 67%;
        background-color: transparent;
        transition: background-size 0.1s linear;
        position: relative;
        width: 100%;
        height: 100%;
        display: inline-block;
        background-repeat: no-repeat;
        background-position: 50%;
        border: none;
        outline: none;
      }
      &__btn:focus {
        outline: none;
      }
    }
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }
}

.modal-enter-active {
  animation: modal 0.5s ease-out;
}

.modal-leave-active {
  animation: modal 0.5s ease-in reverse;
}

@keyframes modal {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@media screen and (min-width: 576px) {
}

// tablet
@media screen and (min-width: 767px) {
  .shedule {
    .servKind {
      font-size: 1.7rem;
    }

    .bookname {
      :nth-child(1) {
        font-size: 1.6rem;
      }
      :nth-child(2) {
        font-size: 1.6rem;
      }
      :nth-child(3) {
        font-size: 1.6rem;
      }
    }
  }
}

@media screen and (min-width: 991px) {
}

// как только мы достигаем min 1200px (увеличивая экран) и больше
@media screen and (min-width: 1200px) {
  .shedule {
    &__litera {
      font-size: 2.25rem;
    }
    .rus {
      font-size: 2rem;
    }
    .servKind {
      font-size: 1.8rem;
    }
    .velPyatok {
      color: #800080;
    }
    .bookname {
      :nth-child(1) {
        font-size: 1.8rem;
      }
      :nth-child(2) {
        font-size: 1.8rem;
      }
      :nth-child(3) {
        font-size: 1.8rem;
      }
    }
  }
}
</style>
